                    var endTime = startTime + duration;
                    var startPos = this.camera.position.clone().normalize();
                    var endPos = to.clone().normalize();
                    var length = this.camera.position.length();
                    var easeInOut = function (t) {
                        t *= 2;
                        if (t < 1)
                            return (t * t * t) / 2;
                        t -= 2;
                        return (t * t * t + 2) / 2;
                    };
