                            t = 1;
                        t = easeInOut(t);
                        var pos = new THREE.Vector3()
                            .add(startPos.clone().multiplyScalar(1 - t))
                            .add(endPos.clone().multiplyScalar(t))
                            .normalize()
                            .multiplyScalar(length);
